#pdf {
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    user-select: none;
    position: relative;
    overflow: visible;
    padding: 2em;
    font-size: 12pt;
    font-family: 'Times New Roman',Times,sans-serif;
    background: #fff;
    text-align: left;
    color: #000;
    word-wrap: break-word;
    word-break: break-word;
    overflow-x: hidden;
}